.sticky-nav {
  position: sticky;
  top: 0px;
  background-color: white;
}

.header-container {
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  border-bottom: 1px solid #ccc;

  .logo, a {
    font-size: 16px;
    color: #35c5f0;
    text-decoration: none;
  }

  .logo {
    padding: 15px 15px 15px 0;
  }

  button {
    font-size: 16px;
    border: none;
    box-shadow: none;
    background-color: transparent;
    cursor: pointer;
    color: #35c5f0;

    &:focus {
      outline: none;
      box-shadow: none;
    }
  }


  .nav {
    display: flex;

    .nav-item {
      padding: 15px;
    }

    // .nav-item:not(:last-child) {
    //   margin: 0 5px;
    // }

    // .nav-item:last-child {
    //   margin: 0 0 0 5px;
    // }


    .dictionary:hover {
      background-color: #eee;
    }

    .nav-button-container {

      .nav-button:hover {
        background-color: #eee;
        border-radius: 5px;
      }
    }
  }
}

.input-container {

  .input-area {
    display: flex;
    
    margin-top: 5px;
    margin-bottom: 5px;
    
    input {
      flex: 80%;
      width: 90%;
      height: 4rem;
      font-size: 2rem;
    }
  }

  .day-button {
    width: 100px;
    height: 50px;
    background-color: #61dafb;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px;
  }

  .uploadBtn {
    width: 200px;
    height: 100px;
    font-size: 2rem;
    background-color: #61dafb;
    border: none;
    border-radius: 5px;;
    cursor: pointer;
  }
}

select {
  width: 90%;
  height: 2rem;
}

.dayButton-container {
  display: flex;
  flex-wrap: wrap;

  .dayButton {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #35c5f0;
    width: 100px;
    height: 100px;
    border-radius: 10px;
    margin: 10px;
    cursor: pointer;

  }

  .hoverEvent {
    background-color: grey;
  }
}

.checkbox-container {
  overflow: scroll;

  &.off {
    background-color: #eee;
    color: #ccc;
  }
  
  .kor-off {
    opacity: 0;
  }

  .word-btn-container {

    .word-btn {
      background-color: #85d2e9;
      font-size: 0.7em;
      width: 49%;
      height: 100%;
      border: none;
      cursor: pointer;
      border-radius: 5px;

      &:first-child {
        border-right: 1px solid #eee;
        margin-right: 1px;
      }

      &:hover {
        background-color: #3bbee6;
      }

      &:last-child {
        background-color: rgba(180, 50, 50, 0.582);
      }

      &:last-child:hover {
        background-color: rgba(255, 0, 0, 0.486);
      }
    }
  }
}

.add-word-btn {
  width: 150px;
  height: 75px;
  font-size: 1.7rem;
  background-color: #61dafb;
  border: none;
  border-radius: 5px;;
  cursor: pointer;
  margin: 20px 0;

  &:hover {
    background-color: #3bbee6;
  }
}

.all-hide-btn {
  width: 150px;
  height: 75px;
  font-size: 1.3rem;
  background-color: #61dafb;
  border: none;
  border-radius: 5px;;
  cursor: pointer;
  margin: 20px 0;

  &:hover {
    background-color: #3bbee6;
  }
}

table {
  border-collapse: collapse;
  width: 100%;
  overflow: scroll;
}

table td {
  width: 25%;
  height: 70px;
  border: 1px solid #ccc;
  text-align: center;
  font-size: 26px;
  margin: 0;
}

table td:first-child {
  width: 10%;
}



.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
